body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 480px;
  height: 800px;
  margin: 2rem auto;
  background-image: url(/weather-app/static/media/app.3a3fd1dc.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  transition: 0.2s ease;
  border-radius: 10px;
  box-shadow: 3px 3px 10px rgba(0,0,0,0.5);
}

.App:hover{
  box-shadow: 3px 3px 20px rgba(0,0,0,0.5);
}

/* for class to check the weather */
.App.hot{
  background-image: linear-gradient(to bottom,
  rgba(250, 246, 243, 0.8),
  rgba(0, 0, 0, 0.1)),
  url(/weather-app/static/media/hot.50a7c4ff.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  transition: 0.2s ease;
}

.App.cold{
  background-image: linear-gradient(to bottom,
  rgba(250, 246, 243, 0.8),
  rgba(0, 0, 0, 0.1)),
  url(/weather-app/static/media/cold.bb80d9cd.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  transition: 0.2s ease;
}

.App.rain{
  background-image: linear-gradient(to bottom,
  rgba(250, 246, 243, 0.8),
  rgba(0, 0, 0, 0.1)),
  url(/weather-app/static/media/rain.09c9f3d4.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  transition: 0.2s ease;
}

main {
  min-height: 100px;
  padding: 25px;
}

.search-container {
  width: 100%;
  margin: 0 0 75px;
}

.search-bar {
  color: black;
  font-size: 16px;
  display: block;
  width: 100%;
  padding: 15px;
  border: none;
  outline: none;
  -webkit-appearance: none;
          appearance: none;
  border-radius: 5px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3);
}

.search-container .search-bar:focus{
  background-color: white;
}

.location-container{
  color: white;
  font-size: 30px;
  text-align: center;
  text-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
}

.location-container .date{
  color: white;
  font-size: 20px;
}

.weather-container{
  text-align: center;
}

.weather-container .temperature {
  color: white;
  position: relative;
  display: inline-block;
  margin: 30px auto;
  padding: 15px 25px;
  font-size: 100px;
  font-weight: bold;
  border: 2px solid white;
  border-radius: 5px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
  text-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3);
}

.weather-container .weather {
  color: white;
  font-size: 50px;
  font-weight: bold;
  text-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3);
}
